"use client";
import React from "react";
import CustomImage from "../ui/CustomImage";

export default function HeaderNav() {
  return (
    <div className="px-[20px] w-full">
      <div className="container">
        <div className="w-full h-[90px] bg-black text-white flex justify-between items-center">
          <ul className="flex items-center">
            <li>
              <CustomImage
                src="/common/brandLogo.svg"
                width={245}
                height={52}
                alt="logo"
              ></CustomImage>
            </li>
            <li className="font-nekstlight text-[23px] flex items-center ml-[97px]">
              <p className="mr-[8px]">en</p>
              <CustomImage
                src="/common/HeaderNav/arrow.svg"
                width={14}
                height={7}
                alt="arrow_down"
              ></CustomImage>
            </li>
          </ul>
          <ul className="flex font-nekstmedium text-[25px] space-x-[25px]">
            <li>
              <button className="hover:text-purple-600 hover:scale-[0.9] transition-all duration-[0.3s]">
                features
              </button>
            </li>
            <li>
              <button className="hover:text-purple-600 hover:scale-[0.9] transition-all duration-[0.3s]">
                prices
              </button>
            </li>
            <li>
              <button className="hover:text-purple-600 hover:scale-[0.9] transition-all duration-[0.3s]">
                partners
              </button>
            </li>
            <li>
              <button className="hover:text-purple-600 hover:scale-[0.9] transition-all duration-[0.3s]">
                knowledge
              </button>
            </li>
            <li>
              <button className="hover:text-purple-600 hover:scale-[0.95] duration-[0.3s]">
                academy
              </button>
            </li>
          </ul>
          <button className="w-[144px] h-[52px] border-solid border-[3px] border-purple-600 rounded-[30px] font-nekstmedium text-[25px] hover:bg-purple-600 hover:scale-[1.1] duration-[0.2s]">
            sign in
          </button>
        </div>
      </div>
    </div>
  );
}
